
body{
    margin: 0%;
    padding: 0%;
    /* overflow: hidden; */
}
.container{
background: linear-gradient(rgb(109, 199, 154), rgb(220, 224, 165));
width: 100vw;
height: 100vh;
margin: 0%;
}

h1{
    margin: 0px ;
    text-align: center;
    padding-top: 20px;
    font-size: calc(10px + 5vw);
}
.table{
    margin:10vw 2vw  ;
}

table,tr,td,th{
    border: 1px solid rgb(104, 102, 102);
    border-collapse: collapse;
    text-align: center;
  }

  th,td{
    width: 30vw;
    min-width: 30px;
    height: 10vh;
    padding: 2vw;
  
  }
  th{
    font-size: calc(1px + 3vw);
 
  }
.link{
    text-decoration: none;
    border: none;
}

  .table_word{
   position: relative;
   padding: 4vw;
   font-size: calc(5px + 2vw);
   text-transform: uppercase;
   color: rgb(13, 24, 85);
   font-weight: 700;
   font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
     }

     .table_word .hovertext{
         visibility: hidden;
      
         margin: 0%;
         background-color: white;
         color: rgb(68, 57, 134);
         text-align: center;
         padding:0 5px;
         border-radius: 10px;
         position: absolute;
         top: 90px;
         left: 15px;
         font-size: 1.3rem;
         padding: 10px;
         z-index: 1;
     }

     .table_word:hover .hovertext{
         visibility:visible ;
     }

     .data_phonetics_length, .data_meanings_length{
       font-size: calc(2px + 2vw);
     }

     .table_word .hovertext::after{
         content: " ";
         position: absolute;
         bottom: 100%;
         left: 50%;
         margin-left: -5px;
         border: 5px solid ;
         border-color: transparent transparent  white transparent;

     }